import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Summer from '@/components/Summer'
import Rainy from '@/components/Rainy'
import Autumn from '@/components/Autumn'
import LateAutumn from '@/components/LateAutumn'
import Winter from '@/components/Winter'
import Spring from '@/components/Spring'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/summer',
      name: 'summer',
      component: Summer
    },
    {
      path: '/rainy',
      name: 'rainy',
      component: Rainy
    },
    {
      path: '/autumn',
      name: 'autumn',
      component: Autumn
    },
    {
      path: '/late_autumn',
      name: 'late_autumn',
      component: LateAutumn
    },
    {
      path: '/winter',
      name: 'winter',
      component: Winter
    },
    {
      path: '/spring',
      name: 'spring',
      component: Spring
    }
  ]
})



