<template>
  <div>
    <!-- Loader -->
    <div class="lds-ring" v-if="loader"><span id="loaderText">Loading</span><div></div><div></div><div></div><div></div></div>
    <!-- Skill Section Start -->
    <div id="skillSection" v-if="dataLoadSignal">
      <b-container>
        <div class="skill-cards">
          <b-row v-for="row in formattedClubsSkills" :key="row" class="skill-row">
            <b-col cols="12" sm="12" md="" lg="" v-for="item in row" :key="item">
              <router-link :to="item.url">
                <vue-flip active-hover="true" class="card html-card">
                  <div slot="front" class="slot">
                    <img :src="item.img">
                    <h2>{{ item.name }}</h2>
                  </div>
                  <div slot="back" class="slot">
                    <vue-circle
                      :progress="item.progress"
                      :size="100"
                      :reverse="false"
                      line-cap="circle"
                      :fill="item.fill"
                      empty-fill="rgba(255, 255, 255, 1)"
                      :animation-start-value="0"
                      :start-angle="60"
                      insert-mode="append"
                      :thickness="5"
                      :show-percent="true"
                      @vue-circle-progress="progress"
                      @vue-circle-end="progress_end"
                      class="vue-circle"
                    >
                    </vue-circle>
                    <!-- <img :src="club.img"> -->
                    <!-- <h2><a href="#">{{ club.name_b }}</a></h2> -->
                  </div>
                </vue-flip>
              </router-link>
            </b-col>
          </b-row>
          <!-- <router-link to="skills" class="skill-btn">
            <a class="button">See more skills</a>
          </router-link> -->
        </div>
      </b-container>
    </div>
    <!-- Skill Section End -->
  </div>
</template>
<script>
import { db } from '../firebase.js';
import Vue from 'vue';
import VueFlip from 'vue-flip';
import VueCircle from 'vue2-circle-progress';

export default {
  components: {
    'vue-flip': VueFlip,
    VueCircle
  },
  data() {
    return {
      jobRetriveData: [],
      loader: false,
      dataLoadSignal: true,
      seasonData: [
          { name:'SUMMER', name_b:'VIEW DETAILS', url: 'summer', progress: 60, fill: '#fece00', img: require('../assets/icon/season/summer.png')},
          { name:'MONSOON', name_b:'VIEW DETAILS', url: 'rainy', progress: 80, fill: '#13c0e5', img: require('../assets/icon/season/rainy.png')},
          { name:'AUTUMN', name_b:'VIEW DETAILS', url: 'autumn', progress: 70, fill: '#f7af25', img: require('../assets/icon/season/autumn.png')},
          { name:'LATE AUTUMN', name_b:'VIEW DETAILS', url: 'late_autumn', progress: 60, fill: { gradient: ["#000", "#41b883"] }, img: require('../assets/icon/season/late_autumn.png')},
          { name:'WINTER', name_b:'VIEW DETAILS', url: 'winter', progress: 40, fill: { gradient: ["#41b883", "#000000"] }, img: require('../assets/icon/season/winter.png')},
          { name:'SPRING', name_b:'VIEW DETAILS', url: 'spring', progress: 50, fill: 'crimson', img: require('../assets/icon/season/spring.png')}
      ]
    }
  },
  firestore(){
    return {
      jobRetriveData: db.collection('jobs').orderBy('createdAt','asc')
    }
  },
  methods: {
    
  },
  computed: {
    formattedClubsSkills() {
        return this.seasonData.reduce((c, n, i) => {
          if (i % 3 === 0) c.push([]);
          c[c.length - 1].push(n);
          return c;
        }, []);
    }
  },
  watch: {
    'jobRetriveData': function(){
      if(this.jobRetriveData.length > 1){
        this.loader = false;
        this.dataLoadSignal = true;
      }
    }
  }
}
</script>
<style scoped>
/** Page Media Query Start */
@media (min-width: 1200px){
  
}
@media screen and (max-width: 991px) {
  
}
@media screen and (max-width: 767px) {
  
}
@media screen and (max-width: 479px) {
  
}
/** Page Media Query End */
</style>



